import dayTemplate from './templates/day.html';

export function bcDayDirective(
) {
    'ngInject';

    const directive = {
        restrict: 'E',
        replace: true,
        scope: {},
        bindToController: {
            bcCollection: '=',
        },
        templateUrl: dayTemplate,
        controller: () => {

            /*
             *console.log(this.bcCollection);
             */

        },
        controllerAs: 'vm',
    };

    return directive;

}




