import monthTemplate from './templates/month.html';

export function bcMonthDirective(
) {
    'ngInject';

    const directive = {
        restrict: 'E',
        replace: true,
        scope: {},
        bindToController: {
            bcCollection: '=',
        },
        templateUrl: monthTemplate,
        controller: () => {

            /*
             *console.log(this);
             */

        },
        controllerAs: 'vm',
        link: linkFunction,
    };


    /**
     * Link
     */
    function linkFunction($scope, $element, $attrs, vm) {
        console.log('In link: ', $scope.bcCollection);
    }


    return directive;


}




